export function getNodesInTree(id, dataArr) {//
    let temp = [];
    for (let i = 0; i < dataArr.length; i++) {
        let rootNode = dataArr[i];
        const getPath = function getPathFun(node) {
            //temptrue
            if (node.id === parseInt(id)) {
                temp.push(node);
                return true;
            }
            //child
            if (node.children && node.children.length > 0) {
                //childchild
                for (let i = 0; i < node.children.length; i++) {
                    let result = getPath(node.children[i]);
                    //
                    if (result) {
                        temp.push(node);
                    }
                }
            }
            return false;
        };

        try {
            getPath(rootNode);
        } catch {
        }
    }

    return temp;
}

import {Notification} from 'element-ui'

export function showNotify(response, operationName) {
    if (response.data.result) {
        Notification({
            message: operationName + '',
            type: 'success',
            duration: 1500,
        });
    } else {
        Notification({
            message: operationName + '',
            type: 'error',
            duration: 1500,
        });
    }
}
