import router from './index'
import store from '../store'
import {userAuth} from 'src/utils/auth' // 

router.beforeEach((to, from, next) => {
  console.log(from.path, to.path);
  if (userAuth.getToken()) { // token
    if (!store.getters.id || store.getters.id === "") { // user_info
      store.dispatch('GetUserInfo').then(res => { // user_info
        const pages = res.data.user.pages;
        const isAdmin = res.data.user.isAdmin;
        store.dispatch('GenerateRoutes', pages, isAdmin).then(() => { // 
          router.addRoutes(store.getters.addRouters); // 
        });
        next();
      }).catch(() => {
        store.dispatch('Logout').then(() => {
          console.log('');
          if (to.path !== "/login") {
            next({path: "/login"});
          } else {
            next();
          }
        })
      })
    } else {
      next();
    }
  } else {
    if (to.path !== "/login") {
      next({path: "/login"});
    } else {
      next();
    }
  }

});

router.afterEach(() => {

});
