import axios from 'axios'
import {Message} from 'element-ui'
import {userAuth} from 'src/utils/auth'
import store from "../store";
import router from "../router";

const newAxios = axios.create({// axios
  baseURL: process.env.BASE_API, // apibase_url
  timeout: 10000 // ,
});

// request
newAxios.interceptors.request.use(request => {
  request.headers['Authorization'] = userAuth.getToken(); // Authorizationcookies
  request.method = 'post';// 
  return request
}, error => {
  console.log('requestError', error);
  return Promise.reject(error)
});

// response
newAxios.interceptors.response.use(
  response => {
    // if (response.data.code === 401) {
    //   this.$router.push({path: '/login'});
    //   return;
    // }
    let token = response.headers['Authorization'];
    if (token === undefined || token === null || token.length === 0) {
      token = response.data.token;
    }
    if (token !== undefined && token !== null && token.length > 0) {
      userAuth.setToken(token);//cookieAuthorization
    }
    return response
  },
  error => {
    console.log('responseError:' + error);// for debug
    Message.error(error.message);
    return Promise.reject(error)
  });

export default newAxios
